import {profileAPI} from "../api/api";
import {toggleIsFetching} from "./usersReducer";


const ADD_POST = 'ADD-POST'
const SET_USER_PROFILE = 'SET_USER_PROFILE'
const SET_USER_STATUS = 'SET_USER_STATUS'
const UPDATE_PROFILE_INFORMATION = 'UPDATE_PROFILE_INFORMATION'
const TOGGLE_IS_DONE = 'TOGGLE_IS_DONE'

let initialState = {
    posts: [
        {
            id: 1,
            message: 'Hi how are you',
        },
        {
            id: 2,
            message: 'Its my first post'
        }
    ],
    profile: null,
    status: '',
    lookingForAJob: false,
    aboutMe: null,
    fullName: null,
    lookingForAJobDescription: null,
    isDone: false
}

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST:
            return {
                ...state,
                posts: [...state.posts, {id: 1, message: action.postText}],
                newPostText: ''
            }
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            };
        case SET_USER_STATUS:

            return {
                ...state,
                status: action.status
            };
        case UPDATE_PROFILE_INFORMATION:

            return {
                ...state,
                ...action.payload
            };
        case TOGGLE_IS_DONE:
            return {
                ...state,
                isDone: action.isDone
            }
        default:
            return state;
    }

}

export const addPostActionCreator = (postText) => ({
    type: ADD_POST,
    postText
})

export const setUserProfile = (profile) => ({
    type: SET_USER_PROFILE,
    profile
})
export const setUserStatus = (status) => ({
    type: SET_USER_STATUS,
    status
})
export const updateProfileInformation = (aboutMe, lookingForAJobDescription, fullName, lookingForAJob) => ({
    type: UPDATE_PROFILE_INFORMATION,
    payload: {
        aboutMe,
        lookingForAJobDescription,
        fullName,
        lookingForAJob
    }
})
export const toggleIsDone = (isDone) => {
    return {
        type: TOGGLE_IS_DONE,
        isDone
    }
}

export const getUserProfile = (userId) => {
    return async (dispatch) => {
        let response = await profileAPI.getProfile(userId)
        dispatch(setUserProfile(response.data))
    }
}
export const getUserStatus = (userId) => {
    return async (dispatch) => {
        let response = await profileAPI.getStatus(userId)
        dispatch(setUserStatus(response.data))
    }
}
export const updateStatus = (status) => {
    return async (dispatch) => {
        let response = await profileAPI.updateStatus(status)
        if (response.data.resultCode === 0) {
            dispatch(setUserStatus(status))
        }
    }
}
export const setProfileInformation = (aboutMe, lookingForAJobDescription, fullName, lookingForAJob,) => {
    return async (dispatch) => {
        dispatch(toggleIsDone(true))
        let response = await profileAPI.updateProfile(aboutMe, lookingForAJobDescription, fullName, lookingForAJob)
        if (response.data.resultCode === 0) {
            let {aboutMe, lookingForAJobDescription, fullName, lookingForAJob} = response.data.data;
            dispatch(updateProfileInformation(aboutMe, lookingForAJobDescription, fullName, lookingForAJob, true))
            dispatch(toggleIsDone(false))
        }
    }
}


export default profileReducer;