import React from "react";
import {Field, reduxForm} from "redux-form";
import {Input} from "../../common/FormsControls/FormsControls";
import {required} from "../../../utils/validators/validators";
import {connect} from "react-redux";
import {Redirect} from "react-router-dom";
import s from '../../common/FormsControls/FormsControls.module.css'
import {setProfileInformation} from "../../../redux/profileReducer";

const UpdateProfileInfoForm =(props) =>{
    return <div>
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field placeholder={"About me"} name={"aboutMe"} component={Input} validate={required} />
            </div>
            <div>
                <Field placeholder={"Description of job that you want to find"} name={"lookingForAJobDescription"} component={Input} validate={required} />
            </div>
            <div>
                <Field placeholder={"full Name"} name={"fullName"}
                       component={Input} validate={required} />
            </div>
            <div>
                <Field component={"input"} name={"lookingForAJob"} type={"checkbox"}/> do you looking for job?
            </div>
            {props.error && <div className={s.formSummaryError}>
                {props.error}
            </div>}
            <div>
                <button>Login</button>
            </div>
        </form>
    </div>
}

const LoginReduxForm = reduxForm ({form: 'updateProfileInfo'})(UpdateProfileInfoForm)

const UpdateProfileInfo = (props) => {
    const onSubmit = (values) =>{
        props.setProfileInformation(values.aboutMe,
            values.lookingForAJobDescription,
            values.fullName,
            values.lookingForAJob
        );
    }
    if(props.isDone){
        return <Redirect to={'/profile'}/>
    }
    return <div>
        <h1>Update Profile Information</h1>
        < LoginReduxForm onSubmit ={onSubmit}/>
    </div>
}
const mapStateToProps=(state) =>{
    return{
        isDone: state.profilePage.isDone
    }
}

export default connect(mapStateToProps, {
    setProfileInformation
}) (UpdateProfileInfo);